@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  /* background-color: #f2f6f9; */
}

.home,
.reports,
.products {
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
}

.navbar {
  background-color: #060b26;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: center;
          align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: center;
          align-items: center;
}

span {
  margin-left: 16px;
}

.home{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    background-color:#f2f6f9;
}
.{
    background-color: #f2f6f9;
}
.card *{
    /* border: 1px solid black; */
    margin:0; 
}
.card{
    border: solid .5px rgb(185, 181, 181);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 10px;
    width: 300px;  
    overflow: hidden;
    margin-top: 10px;
    height: 500px;
    transition: all 0.5s;
    box-shadow: 0 4px 8px 0 
            rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card:hover{
    margin-top: 1px;
}
.card .cardTop{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;;
    -webkit-align-items: center;;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    /* padding: 30px 0 25px 0; */
    /* width: 100%; */
    height: 60%;
    position: relative;

}
.card .cardTop p{
    font-size:12px;
    color: rgb(155, 153, 145);
}
.card .cardLower{
    /* background: #ca76e5; */
    color: azure;
    height: 40%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;;
    -webkit-align-items: center;;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: -webkit-fill-available;
    width: stretch;
    position: relative;

}
.card .cardLower h2{
    padding: 13px;
    font-size: 40px;
}
.card .cardLower p{
    font-size: 14px;
    padding: 2px;
}
.card .cardLower .cardButton{
    background-color: azure;
    border: none;
    margin: 16px;
    font-size: 20px;
    padding: 3px 10px 3px 10px;
    border-radius: 20px ;
    transition: all 0.5s;
    cursor: pointer;
}

